 import {useHttp} from "@/utils/http";

 //
 interface RecentPopularPests {
    bugType:string,
    bugInfo:string,
   // 
    bugImg:Array<string>,
    bugId:number
 }
 //
 const config ={ 
    url:"/bugs/hot",
    data :{}
 }

 export const useGetHotPests =()=>{
    const {data,state} =useHttp<{},RecentPopularPests[]>(config)

    return {data,state}
 } 