// 
import {useHttp} from "@/utils/http";


interface GetCollectionAnswersProps {
    questionId:number,
    pageNum:number,
    pageSize:number
}

// 
interface CollectionAnswer {
    // 
    question:string,
    answerId:number,
    answerContent:string,
    answerTime:string,
    answerPraise:string,
    openid:string,
    answerImg:string,
    answerName:string
    // openid
}

const config ={
    url:'/answers',
    data:{
       questionId:1,
       pageNum:1,
       pageSize:1
    }
}

export const useGotCollectionAnswers =()=>{
    const {data,state} =useHttp<GetCollectionAnswersProps,CollectionAnswer[]>(config)
    // const {data,state} =useHttp<{},CollectionArticle[]>(config,undefined)

    return {
        data,
        state
    }
}