 import {useHttp} from "@/utils/http";

// expertId
interface GetExpertsProps {
// apifoxexpertId
    expertId:number,
    pageNum: number,
    pageSize: number
}

// 
interface professionTeam {
    expertId:string,
    expertName:string,
    expertBrief:string,
    expertInfo:string,
    expertImg:string
}

const config ={
    url:'/experts',
    data:{
        expertId:2,
        pageNum:1,
        pageSize:4
    }
}

export const useGotProfessionTeam =()=>{
    // dataprofessionTeam[],professionTeam
    // emmmts
    const {data,state} =useHttp<GetExpertsProps,professionTeam[]>(config,undefined)

    return {
        data,
        state
    }
}