// 
import {useHttp} from "@/utils/http";

// 
interface GetExpertsProps {
    pageNum: number,
    pageSize: number
}

// 
interface professionTeam {
    expertId:string,
    expertName:string,
    expertBrief:string,
    expertInfo:string,
    expertImg:string
}

const config ={
    url:'/experts',
    // 
    data:{
        pageNum:1,
        pageSize:4
    }
}

export const useGotProfessionTeam =()=>{
    const {data,state} =useHttp<GetExpertsProps,professionTeam[]>(config,undefined)

    return {
        data,
        state
    }
}